import { createClient } from "@supabase/supabase-js";

const SUPABASE_URL = "https://dqfdkblzexlyikggnzgf.supabase.co";
const SUPABASE_KEY =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImRxZmRrYmx6ZXhseWlrZ2duemdmIiwicm9sZSI6InNlcnZpY2Vfcm9sZSIsImlhdCI6MTc1NzExMjc5NiwiZXhwIjoyMDcyNjg4Nzk2fQ.yKKxTXYf-WXIrFeunLRRiEC3my74ffifm9pWwobW7xc";

const supabase = createClient(SUPABASE_URL, SUPABASE_KEY);

/**
 * Return the SUM of the numeric Value field for given filters
 * @customfunction
 */


export async function VALUE(
  Scenario: string,
  Period: string,
  Entity: string,
  Account: string,
  Department: string,
  Currency: string,
  Subline: string
): Promise<number> {
  try {
    let query = supabase.from("planxls_data").select("Value");

    if (Scenario && Scenario.trim() !== "") query = query.eq("Scenario", Scenario);
    if (Period && Period.trim() !== "") query = query.eq("Period", Period);
    if (Entity && Entity.trim() !== "") query = query.eq("Entity", Entity);
    if (Account && Account.trim() !== "") query = query.eq("Account", Account);
    if (Department && Department.trim() !== "") query = query.eq("Department", Department);
    if (Currency && Currency.trim() !== "") query = query.eq("Currency", Currency);
    if (Subline && Subline.trim() !== "") {
      query = query.eq("Subline", Subline);
    } else {
      // Default to a subset of Subline values to limit to 2–4 records
      query = query.in("Subline", ["Product 1", "Product 2", "Product 3", "Product 4"]);
    }

    const { data, error } = await query;

    if (error) {
      console.error("Supabase VALUE error:", error.message);
      return NaN;
    }

    if (!data || data.length === 0) return NaN;

    // Log the number of records for debugging
    console.log("VALUE query result count:", data.length);

    // Sum the Value column
    return data.reduce((sum, row) => {
      const num = Number(row.Value);
      return sum + (isNaN(num) ? 0 : num);
    }, 0);
  } catch (err) {
    console.error("VALUE exception:", err);
    return NaN;
  }
}

/**
 * Return all matching rows as a 2D array for Excel
 * @customfunction
 */


export async function GetXLS(
  Scenario: string,
  Period: string,
  Entity: string,
  Account: string,
  Department: string,
  Currency: string,
  Subline: string
): Promise<string[][]> {
  try {
    let query = supabase.from("planxls_data").select("*");

    if (Scenario && Scenario.trim() !== "") query = query.eq("Scenario", Scenario);
    if (Period && Period.trim() !== "") query = query.eq("Period", Period);
    if (Entity && Entity.trim() !== "") query = query.eq("Entity", Entity);
    if (Account && Account.trim() !== "") query = query.eq("Account", Account);
    if (Department && Department.trim() !== "") query = query.eq("Department", Department);
    if (Currency && Currency.trim() !== "") query = query.eq("Currency", Currency);
    if (Subline && Subline.trim() !== "") {
      query = query.eq("Subline", Subline);
    } else {
      // Add a filter to limit Subline to specific values
      query = query.in("Subline", ["Product 1", "Product 2", "Product 3", "Product 4", "Product 5", "Product 6", "Product 7", "Product 8"]);
    }

    const { data, error } = await query;

    if (error) {
      console.error("Supabase GetXLS error:", error.message);
      return [["Error: " + error.message]];
    }

    if (!data || data.length === 0) return [["No result found"]];

    const headers = Object.keys(data[0]);
    const rows = data.map((row) => headers.map((h) => String(row[h] ?? "")));

    return [headers, ...rows];
  } catch (err) {
    console.error("GetXLS exception:", err);
    return [["Error"]];
  }
}

// Recalculate all formulas in workbook
function rerunFormula(event: Office.AddinCommands.Event) {
  Excel.run(async (context) => {
    context.workbook.application.calculate("Full");
    await context.sync();
  })
    .catch((err) => console.error(err))
    .finally(() => event.completed());
}

Office.actions.associate("rerunFormula", rerunFormula);

CustomFunctions.associate("VALUE", VALUE);
CustomFunctions.associate("GETXLS", GetXLS);